.container {
    width: 10%;
    display: flex;
    position: absolute;
    top: 10px;
    left: 0;
    flex-direction: column;
    align-items: center;
}

.text {
    flex: 1;
    display: flex;
    align-items: center;
}

@media (max-width: 600px) {
    .container {
        width: 20%;
    }

}